
import XButton from 'vux/src/components/x-button/index.vue'

import { mapState } from 'vuex';
export default {
components: {
XButton
},
data: function() {
return {
fileList: [
{
filename: '理综试卷.pdf',
filetype: 'pdf',
number: 1,
size: ['A4'],
color: ['黑白'],
page: ['单面'],
composite: []
},
{
filename: '准考证.png',
filetype: 'png',
number: 1,
size: ['A4'],
color: ['黑白'],
page: ['单面'],
composite: []
}
]
};
},
computed: {
...mapState({
storeData: (state) => state.printData.storeData
})
},
methods: {
submitForm: function(type) {}
},
created: function() {
if (!this.storeData.value) {
this.$router.push({ path: '/chiocestore' });
return;
}
},
methods: {
onFileChange() {
console.log(this.$refs.fileArr);
console.log(this.$refs.fileArr.files);
const file = this.$refs.fileArr.files[0];
this.fileList.push({
filename: file.name,
filetype: file.type,
number: 1,
size: ['A4'],
color: ['黑白'],
page: ['单面'],
composite: []
});







},
onDeleteFile(index) {
this.fileList.splice(index, 1);
},
onSubmitFile() {
if (this.fileList.length == 0) {
this.$vux.toast.show({
text: '请选择文件',
type: 'text'
});
return;
}

let printData = { ...this.$store.state.printData };
printData.fileData = this.fileList;

this.$store.dispatch('changePrintdata', printData);
this.$router.push({ path: '/setconfig' });
},
onBack() {
this.$router.push({
path: '/chiocestore'
});
}
}
};



